import React, { Component } from 'react';

class LifeCycle extends Component {

    constructor(props) {
        super(props);
        console.log("called constructor Method")
        this.state = {
            name : 'Mr.G'
        }
    }
    
    componentWillMount(){
        console.log("called componentWillMount Called")
    }
    
    componentDidMount(){
        console.log("called componentDidMount called")
    }

    onChangeName(){
        this.setState({
            name : 'Mr.A'
        })
    }

    shouldComponentUpdate(nextprops, nextstate){
        console.log(nextprops, nextstate)
        console.log("called shouldComponentUpdate")
        if(this.state.name === this.setState.name){
            return false;
        }else{
            return true;
        }
    }

    componentWillUpdate(){
        console.log("called componentWillUpdate method")
    }

    componentDidUpdate(){
        console.log("called componentDidUpdate method")
    }

    componentWillUnmount(){
        console.log("Called componentWillUnmount method")
        // alert("Thank you for using. Please click ok")       
    }

    render() {
        console.log("called Render Method")

        return (
            <div>
                <h1>This is a life cycle page</h1>
                <h1>User name is : {this.state.name}</h1>
                <button onClick={()=>{this.onChangeName();}}>Change name</button>
            </div>
        );
    }
}

export default LifeCycle;