import React, { Component } from 'react';
import {NavLink} from 'react-router-dom'

class RegisterPage extends Component{

    constructor(props){
        super(props);
        this.state = {
            register : {

                firstName : '',
                lastName : '',
                email : '',
                password : '',
                dateOfBirth : '',
                gender : '',
                hobbies : [],
                address : '',
                city : '',
            },
            error : {

                firstName : false,
                lastName : false,
                email : false,
                password : false,
                dateOfBirth : false,
                gender : false,
                hobbies : false,
                ddress : false,
                city : false
            }
            
        }
    }

    onHandleInput = (event) => {
        if(event.target.type === "checkbox"){
            if(event.target.checked){
                this.state.register.hobbies.push(event.target.value);
            }
            else{
                const index = this.state.register.hobbies.findIndex((value, index) => {
                    return value === event.target.value
                });
                this.state.register.hobbies.splice(index, 1);
            }

            this.setState({
                register : {...this.state.register, hobbies : this.state.register.hobbies}
            })
        }
        else{
        this.setState({
            register : { ...this.state.register, [event.target.name] : event.target.value }
    })
    }
}

createAccount(){

    for(let key in this.state.register){
      if(key === 'hobbies'){
        if(this.state.register.hobbies.length === 0){
          this.state.error.hobbies = true;
        }else{
          this.state.error.hobbies = false;
        }
      }
      else if(this.state.register[key] === ''){
        this.state.error[key] = true; 
      }
      else{
        this.state.error[key] = false; 
      }
    }

    this.setState({
      error : this.state.error
    })

  }
    

    onInputFocus = (event) => {
        this.setState({
            error : {...this.state.error, [event.target.name] : false}
        })
    }
    
    onInputBlur = (event) => {   
        const value = event.target.value;
        if(value.length === 0){
            this.setState({
                error : {...this.state.error, [event.target.name] : true}
            })
        }

    }

    render() {
        return(
            <div className="login-form">
                <h2>Create your account</h2>
                <div>
                    <label className="label">Enter your first name</label>
                    <input type="text" className="input" name="firstName" placeholder="Please enter first name" onChange={this.onHandleInput} onFocus={this.onInputFocus} onBlur={this.onInputBlur}></input>
                    { this.state.error.firstName && 
                    <div>
                        <span className="err-msg">Please enter a valid user first name</span>
                        </div> }
                </div>
                <div className="m-top-15">
                    <label className="label">Enter your Last name</label>
                    <input type="text" className="input" name="lastName" placeholder="Please enter last name" onChange={this.onHandleInput}onFocus={this.onInputFocus} onBlur={this.onInputBlur}></input>
                    { this.state.error.lastName && <div><span className="err-msg">Please enter a valid user last name</span> </div>  }
                </div>
                <div className="m-top-15">
                    <label className="label">Enter your Email</label>
                    <input type="email" className="input" name="email" placeholder="Please enter email" onChange={this.onHandleInput} onFocus={this.onInputFocus} onBlur={this.onInputBlur}></input>
                    { this.state.error.email && <div><span className="err-msg">Please enter a valid Email id</span> </div> }
                </div>
                <div className="m-top-15">
                    <label className="label">Enter your password</label>
                    <input type="password" className="input" name="password" placeholder="Please enter password" onChange={this.onHandleInput} onFocus={this.onInputFocus} onBlur={this.onInputBlur}></input>
                    { this.state.error.password && <div><span className="err-msg">Please enter a valid password</span></div>  }
                </div>
                <div className="m-top-15">
                    <label className="label">Enter your DOB</label>
                    <input type="date" className="input" name="dateOfBirth" onChange={this.onHandleInput} ></input>
                    { this.state.error.dateOfBirth && <div><span className="err-msg">Please enter a valid date of birth</span> </div> }
                </div>
                <div className="m-top-15">
                    <label className="label">Select your gender</label>
                    <input type="radio" name="gender" value="male" onChange={this.onHandleInput}/>male
                    <input type="radio" name="gender" value="female" onChange={this.onHandleInput}/>Female
                    <input type="radio" name="gender" value="do not wish to specify" onChange={this.onHandleInput}/>Others

                    { this.state.error.gender && <div><span className="err-msg">Please select a gender</span> </div> }
                </div>
                <div className="m-top-15">
                    <label className="label">Enter your Hobbies</label>
                    <input type="checkbox" className="m-left-10" name="hobbies" value="cricket" onChange={this.onHandleInput}/>Cricket
                    <input type="checkbox" className="m-left-10" name="hobbies" value="Football" onChange={this.onHandleInput}/>Football
                    <input type="checkbox" className="m-left-10" name="hobbies"value="Volleyball" onChange={this.onHandleInput}/>Volleyball
                    <input type="checkbox" className="m-left-10" name="hobbies" value="Carrom" onChange={this.onHandleInput}/>Carrom
                    { this.state.error.hobbies && <div><span className="err-msg">Please enter any hobbies</span> </div> }
                </div>
                <div className="m-top-15">
                    <label className="label">Enter your address</label>
                    <textarea placeholder="Please enter your address" name="address" onChange={this.onHandleInput} onFocus={this.onInputFocus} onBlur={this.onInputBlur}></textarea>
                    { this.state.error.address && <div><span className="err-msg">Please enter a address</span> </div> }
                </div>
                <div className="m-top-15">
                    <label className="label">Select your city</label>
                    <select className="label" defaultValue="" onChange={this.onHandleInput} name="city">
                    <option value="" disabled>Please select your city</option>
                    <option value="chennai" >Chennai</option>
                    <option value="madurai" >madurai</option>
                    <option value="kovai"  >Kovai</option>
                    <option value="thirunelveli" >Thirunelveli</option>
                    <option value="virudhunagar"  >Virudhunagar</option>
                    <option value="neyveli">Neyveli</option>
                    </select>
                    { this.state.error.city && <div><span className="err-msg">Please select your city</span> </div> }
                </div>
                <div className="m-top-15">
                <button onClick={() => {this.createAccount();}}>Submit</button>
                </div>
                <NavLink to="/">Go back to login page</NavLink>
            </div>

        );
        
    }
}

export default RegisterPage;